#map {
  height: 100vh;
  width: 100vw;
}

.controls {
  display: -ms-flexbox;
  display: flex;

  position: absolute;
  z-index: 10;
  padding: 20px;
}

.controls--input {
  width: 300px;
}

.controls--input:nth-child(n+2) {
  margin-left: 10px;
}

.app--header {
  background-color: white;
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  max-width: 800px;
}

.app--input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid transparent;
  width: 240px;
  height: 28px;
  margin-top: 10px;
  padding: 0 12px;
  border-radius: 3px;
  -webkit-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  font-size: 14px;
  outline: none;
  -o-text-overflow: ellipses;
     text-overflow: ellipses;
}

.loading {
  margin: 40vh auto 0;
  position: absolute;
  text-align: center;
  width: 100vw;
}

@-webkit-keyframes spinner {
  to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes spinner {
  to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

.spinner:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 50%;
  border: 2px solid #ccc;
  border-top-color: #333;
  -webkit-animation: spinner .6s linear infinite;
          animation: spinner .6s linear infinite;
}



